import React from 'react';
import { Descriptions } from 'antd';
import { MdFlightTakeoff, MdFlightLand } from 'react-icons/md';


const PackageView = ({ title }) => (
    <Descriptions className='col-1' title={title} size="small" bordered>
        <Descriptions.Item label="טיסות" span={3}>
            <MdFlightTakeoff /> טיסה ישירה להרקליון, כרתים ב-22.09.24<br/>
            <MdFlightLand /> טיסה ישירה מהרקליון, כרתים לישראל ב-26.09.24<br />
            * העברות משדה התעופה בהרקליון לריזורט ובחזרה
        </Descriptions.Item>
        <Descriptions.Item label="לינה" span={3}>4 לילות על בסיס פנסיון מלא<br/>
        מבשלת צמודה המכינה אוכל יווני ביתי משובח 
        </Descriptions.Item>
        <Descriptions.Item  label= "מחיר" span={3}>
          <b>מחיר מוזל בהרשמה מוקדמת עד ה 31.05.24</b> <br /> 
            חדר זוגי בוילה  - 1350 יורו לאדם <br />
                        <b>מחיר לאחר ה 31.05.24</b> <br />  
            חדר זוגי בוילה - 1485 יורו לאדם   <br />
                         
            

        </Descriptions.Item>
        <Descriptions.Item label="פעילויות" span={3}>
            סיורים בערים עתיקות ועוד הפתעות ! <br />
            וכמובן, את כל סדנאות הפלייבק, הג'אמים והפעילויות בריזורט
        </Descriptions.Item>
        <Descriptions.Item label="לא כלול בחבילה" span={3}>
            <ul>
                <li>ביטוח רפואי וביטוח מטען (רצוי לבצע ביטוח הכולל גם ביטול נסיעה)</li>
                <li>הוצאות אישיות נוספות</li>
                <li>ארוחות אשר לא מפורטות בתוכנית הריטריט</li>
                <li>בדיקות קורונה (אם ידרשו בארץ ו/או בכרתים)</li>
            </ul>
        </Descriptions.Item>
    </Descriptions>
);

export default PackageView;